          </p>

          <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
          ><span style="font:7.0pt 'Times New Roman'"
            >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
          ><span style="font-family:AppleSystemUIFont; font-size:12pt">1. </span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >この規約（以下、「本規約」といいます。）は、株</span
          ><span style="font-family:'Yu Mincho'; font-size:12pt">式会社</span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >QbayTechnology</span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >（以下、「</span
          ><span style="font-family:AppleSystemUIFont; font-size:12pt"
            >QBAY</span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >」といいます。）が提供する</span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >飲食店向けスマホ注文・決済サービス</span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"> </span>
          <span style="font-family:'.PingFang SC'; font-size:12pt"
            >（以下、「本サービス」といいます。）についての利用上の各種条件を定めるものです。</span
          >
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約と</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">別</span
            ><span style="font-family:'Yu Mincho'; font-size:12pt">で</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が定める規約、申込書、覚書及び諸規定（以下、「諸規定」といいます。）は、一体として</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >と加盟店（第３条にて定義）との利用契約（第３条にて定義）の契約条件となるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約と前項の諸規定の内容が異なる場合には、</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >当該諸規定</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の内容が優先して適用されるものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >2</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（規約の変更）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、内容に応じて適切な期間を定めて通知することにより、加盟店（第３条にて定義）の了解を得ることなく本規約を変更することがあります。この場合、本サービスの利用条件は、変更後の規約によるものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >3</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（定義）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約において、以下の各号の用語は、各号所定の意味を有するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >「利用者」とは、商品やサービス等の代金支払いを手段として本サービスを利用する者を意味します。利用者による本サービスの利用方法は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >がサービスの仕様として公開する通りとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >「加盟店」とは、本サービスの利用を申込み、本サービスを利用して、利用者に対して加盟店の商品やサービスの提供を行う個人または法人その他の団体を意味します。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >「利用契約」とは、加盟店が本サービスの利用を</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に申し込んだ場合における、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >との本サービスの利用上の契約関係を意味します。加盟店が</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の販売代理店を介して申し込んだ場合でも、利用契約は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >と加盟店との間に成立します。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >「アカウント」とは、ＩＤ·パスワード等を契機として行使が可能となる加盟店の本サービスの利用権その他利用契約における本サービスの利用上の地位を意味します。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >4</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（利用申込）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスの利用申込にあたり、本サービス内容及び本規約の内容を理解し同意した上で、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">や</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の販売代理店が指定する申込みフォーム·申込み様式により申込むものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、当該申込みを受け、加盟店のためのアカウントの設定を行うものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >5</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（アカウントの種類）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が別途定める種別による本サービスのアカウントを取得して本サービスを利用するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >アカウントの種別により、利用可能な本サービスの内容·利用料金が異なる場合があります。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >6</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（アカウントの管理）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、アカウントの管理及び使用について一切の責任を負うものとし、加盟店においてアカウントの使用上の過誤、管理不十分又は、第三者による不正使用等に起因して加盟店が被害を被った場合においても、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は当該損害につき一切責任を負わないものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店のアカウントによって行われた設定、アクセス、作業その他の行為は、すべて加盟店によってなされたものと見なします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span>
            <span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、以下の各号に該当する場合において、速やかに</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に連絡し、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の指示に従うものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >ユーザ</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >ID</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >を忘れた場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >パスワードを忘れたか、第三者にパスワードが</span
            ><span style="font-family:'Yu Mincho'; font-size:12pt">漏洩</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >した場合。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >7</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（権利義務の譲渡禁止）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本規約に基づき、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して有する権利又は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して負う義務の全部又は一部を第三者に譲渡し、承継させ、又は担保に供することはできません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >8</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（契約上の地位の承継）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店の合併又は会社分割等法定の原因に基づき加盟店の地位の承継があったときは、当該地位を承継した者は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対し、すみやかに、承継の原因となった事実を証明する書類を添えて届け出るものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >9</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（届出事項の変更）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスの利用申込にあたって届け出た、社名（個人事業主の場合における屋号·支店名を含む）、代表者名、本店所在地、代表電話番号、担当者電話番号、担当者メールアドレスに変更が生じた場合、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >所定の方法により速やかに変更の事実を届け出るものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、前項の届出が無いために</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >からの通知またはその他の送付書類が延着または到着しなかった場合における不利益が生じた場合、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対し一切の責任を負わないものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >10</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（利用環境）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスの利用に必要なものとして</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が指定するコンピュータ、インターネットへの接続環境、通信機器、タブレット、スマートフォン、ソフトウェア、レジスター等は、加盟店の責任において準備するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >利用するコンピュータのオペレーティングシステムの設定、またハードウェアの設定や加盟店が本サービスを利用するにあたって必要となるネットワーク環境の整備（ファイアーウォール·ネットワークルーターの設定を含む）は、加盟店が自らの責任において行うものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >11</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（提供地域及び時間）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が利用するコンピュータの設置場所は、本規約で特に定める場合を除き、日本国内のみとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスに関して、日時·期間等は、全て日本時間を標準とします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは、年中無休かつ</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">日</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >24</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >時間利用可能とします。ただし</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が指定する時間帯に行う定期的なメンテナンス作業等の間は、この限りではありません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >12</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（本サービス提供の中断）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、以下のいずれかの事由がある場合において、加盟店に事前に通知することなく、本サービスの提供を中断することがあります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の電気通信設備の保守又は工事のため、やむを得ないとき</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の設置する電気通信設備に障害が発生し、やむを得ないとき</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">3</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店の運営するサービスに障害が発生し、やむを得ないとき</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">4</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >天災及び紛争等により本サービスの提供が不可能となったとき</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">5</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >その他、運用上又は技術上</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >がサービスの一時中断が必要と判断したとき</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >13</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（本サービスの利用）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスを自らの責任において利用するものとします。本サービスの日常的な設定や通常の利用は、加盟店自身が実施するものであり、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >はこれを代わりに行う義務を負いません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、自身の従業員やその他加盟店の業務に従事する者に対して、本サービスの利用方法を指導する義務を負います。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >はこれを代わりに行う義務を負いません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスに登録した情報に関し、かかる情報の閲覧者から問い合わせ、クレーム、請求等がなされた場合には、自らの名義で対応し、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に迷惑をかけないものとします。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に連絡があった場合には、加盟店は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >より当該連絡への対応を引き受けるものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >14</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（著作物·データの利用）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が本サービス上で利用を許諾する</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >又は第三者の著作物·データについては、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が別途利用許諾条件を提示する場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスの運営上</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が取得する各種の情報については、加盟店や個人を特定できない情報とした上であれば、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、本サービスの改善のためや、独立した取引対象として使用することができるものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >15</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（情報の削除）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が次の何れかに該当すると判断した場合、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は加盟店が提供した情報等を削除する事が出来るものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >26</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の禁止事項に該当する場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >その他、本サービスの保守·運営上必要と判断した場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >利用契約が解約または終了したとき、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、加盟店が</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の設備に蓄積したデータを保持する義務を負いません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >16</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（利用料金及びその支払方法）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が有料と定めるアカウントや本サービスの機能を利用する場合は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が別途定める本サービスの月額費用等の利用料金を支払うものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、別途加盟店の求めに応じ、加盟店と個別に合意することにより、有償で、本サービスの利用に必要となる機器や什器を販売したり、翻訳等の役務を提供したり、前項の機能とは異なる機能を提供したり、他の加盟店と異なる提供条件を提供したりする場合があります。この場合の初期費用や月額費用は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が別途定める通りとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前各項の料金·費用·代金等の支払いは、費用発生の翌月末日までに、甲が別途指定する請求·決済代行機関を通じて、同機関が定める方法により支払うものとします。物品販売等については、種類により、先払いが条件となる場合があります。甲が特に指定した場合には、甲が指定する銀行口座に送金して支払うものとし、その場合の振り込み手数料は乙の負担となります。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >17</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（契約有効期間）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >利用契約の有効期間は、申込時に定めた利用開始時から、利用契約が解約されるまでとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、１ヶ月前に</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して通知することにより、利用契約を解約することができるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前項の定めにかかわらず、申込条件によっては、最低利用期間が設定される場合があり、その間は解約することができないものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >18</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（利用の停止）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、加盟店が以下の各号に該当する場合において、加盟店に通知することなく、本サービスの提供を停止する事ができるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >利用契約上の債務支払いの怠り又は、怠る恐れがある場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >26</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の規定による禁止行為を行った場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が本規約に定める加盟店としての義務に違反した場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店として不適当と</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が判断した場合。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
            >
              第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >19</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（利用契約の解除）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、次の事由があるときは、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の判断により利用契約を解除する場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >18</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の規定による本サービスの利用が停止された場合において、加盟店が当該停止の日から</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >ヵ月以内に当該停止の原因となった事由を解消しない場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >18</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の事由がある場合において、当該事由が</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の業務遂行上支障を及ぼすと認められる場合。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、前項の規定により利用契約を解除する場合は、加盟店に対し、予め通知します。ただし緊急やむをえない場合においてはこの限りではありません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >20</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（遅延損害金）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、利用契約に基づく各種料金の支払いを怠った場合は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対し、年利</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >14.6%</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の割合による遅延損害金を支払うものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >21</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >QBAY</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >からの通知）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >から加盟店への通知は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">の</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >Web</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >サイト（管理ページを含みます）上での掲示、電子メール若しくは文書の送付、又はその他</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が適当と判断する方法で行います。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前項の通知は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が当該通知を</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">の</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >Web</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >サイト上での掲示又は電子メール若しくは文書の送付にて行った場合、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >Web</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >サイト上に掲示し、又は電子メール若しくは文書を発送した時点をもって到達したものとみなします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約で事前に通知する期間の指定がない場合は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が通知を発した日から</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >15</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >日を経過した時に、加盟店はその通知を承認したものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >22</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（秘密保持）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の事前の書面による承諾なくして、加盟店契約に関して、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >から口頭又は書面を問わず開示されたアイディア、ノウハウ、発明、図面、写真、仕様、データ等の技術上、営業上、並びに業務上の一切の情報</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >以下、「秘密情報」といいます</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >を本サービスの利用その他加盟店契約の目的以外に使用せず、また第三者に開示、漏洩しないものとします。</span
            >
          </p>
          <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
          ><span style="font:7.0pt 'Times New Roman'"
            >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;
          </span>
          <span style="font-family:AppleSystemUIFont; font-size:12pt">2. </span
          ><span style="font-family:'.PingFang SC'; font-size:12pt"
            >前項の規定にかかわらず、加盟店が次の各号の一に該当することを立証した情報は、秘密情報に含まれないものとします。</span
          >
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >開示され又は知得する以前に公知であった情報</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >開示され又は知得する以前に自らが既に所有していた情報</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">3</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >開示され又は知得した後、自らの責に帰さない事由により公知となった情報</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">4</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >開示され又は知得した後、その秘密情報によらず自らの開発により知得し</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >た情報</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">5</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >開示され又は知得した後、正当な権限を有する第三者から秘密保持の義務を負わず適法に知得した情報</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、自己の役職員又は第三者に秘密情報を使用させた場合、当該役職員又は第三者に本規約と同様の守秘義務を課すとともに、当該役職員</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >退職又は退任後も含む</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >又は第三者が守秘義務に違反することのないように、必要な措置を講じなければならないものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >23</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（個人情報の保護）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、個人情報について、本サービスの円滑な提供を確保する為に必要な期間中、これを複製·保存する事ができるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、個人情報保護に関する法令を遵守し、役員はじめ全ての従業者が、取り扱う個人情報の重要性を認識するとともに、漏洩、盗用、改ざん、滅失及び毀損を防止するための合理的な安全管理措置を執ります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、個人情報を本人以外の者に開示·提供せず、本サービスの提供の為に必要な範囲を超えて利用してはならないものとします。ただし法令上の除外事由に該当する場合には、この限りではありません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >24</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（加盟店の責任）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は本規約のみならず、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が随時通知する内容に従って本サービスを利用するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は本サービスを利用する事業において必要となる食品営業許可等を適切に維持するものとします。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >からの求めがあった場合には、営業許可証の写し等を</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に交付するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスの利用により加盟店が第三者に与えた損害、不利益等に関しては、一切の責任を負うものとします。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が法律上加盟店と並んで共同不法行為責任を負う場合には、加盟店は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >を免責させるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、本サービスの利用により他の第三者との間で紛争が生じた場合、あるいは第三者に対して損害を与えた場合は、自己の費用と責任で損害を賠償し又は紛争を解決するものとし、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対し迷惑をかけたり、損害を与えたりしないものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >5. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が本規約に違反し、違法な行為により</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に損害を与えた場合、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して損害を賠償する責任を負うものとし、本サービスの利用を中止し、再発防止の措置を取るものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >25</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（反社会勢力の排除）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は加盟店に対して、加盟店は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して、それぞれ次の各号について表明し、保証するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >自らの役員又は従業員に暴力団、暴力団関係企業、総会屋等、社会運動等又は特殊知能暴力集団等、その他これらに準ずる反社会的勢力</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >以下総称して、「反社会的勢力」といいます</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の構成員</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >暴力団準構成員等実質的に関与している者等を含みます</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >がいないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >自らの役員又は従業員に暴力団の構成員でなくなった時から</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">5</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >年が経過しない者がいないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">3</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >反社会的勢力の構成員が自らの経営に実質的に関与していないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">4</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >自らの取引先に反社会的勢力</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >実質的に関与している者等含みます</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が存在しないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">5</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >反社会的勢力に対して資金を提供又は便宜を供与する等、自らが反社会的勢力の維持運営に協力、関与していないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">6</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >自らの役員又は従業員が反社会的勢力と社会的に非難されるべき関係を有していないこと</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は加盟店に対して、加盟店は</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に対して、それぞれ自らが又は第三者を利用して次の各号に該当する行為を行わないことを保証するものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >脅迫的な言動又は暴力行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >法的な責任を超えた不当な要求行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">3</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >風説を流布し偽計又は威力を用いて相手方の信用を毀損する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">4</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >相手方の業務を妨害する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">5</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >その他前各号に準ずる行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >及び加盟店は、相手方が前二項に違反した場合、相手方に何ら通告することなく、加盟店契約を解除することができるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >及び加盟店は、前項に基づき、加盟店契約を解除した場合、相手方に損害が生じてもその賠償責任を負わないものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >26</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（禁止事項）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店は、以下各号の行為を行ってはならないものとします。また、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は加盟店が以下の行為を行う恐れがあると判断した場合、加盟店への事前の通知·承諾無しに、本サービスの利用停止等の措置を講じることができるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >他の契約者のアカウントを不正に使用する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >未承諾メール等の法律で禁止される電子メールの送信行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >スパム行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >、他の契約者、その他の第三者又は本サービスに損害を与える行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >5. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >他者の財産や肖像権、第三者の著作権、その他の知的財産権を侵害する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >6. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >他の契約者や第三者を誹謗中傷し名誉等を傷つける行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >7. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >ストーカー行為等の規制等に関する法律に違反する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >8. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >わいせつ、児童ポルノ又は児童虐待に相当する画像、文書等を送信もしくは表示する行為、又はこれらを収録した媒体を販売する行為、又はその送信、表示、販売を想起させる広告を表示又は送信する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >9. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >詐欺等の犯罪に結びつく行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >10.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >他者になりすまし本サービスを利用する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >11.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >公序良俗に反する行為あるいは公序良俗に反する情報を他の契約者に提供する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >12.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前条にて禁止される反社会勢力との関与行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >13.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >選挙の事前運動又はこれに類似する行為及び公職選挙法に抵触する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >14.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >ウイルス等の有害なコンピュータプログラム等を送信または掲載する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >15.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >法令に違反する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >16.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >回線に過度の負担のかかる行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >17.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスの運営に支障を与える行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >18.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前各号に定める行為を助長する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >19.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前各号に該当する恐れがあると弊社が判断する行為</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >20.</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >その他、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が不適切と判断する行為</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >27</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >QBAY</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >の義務及び責任）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、本サービスが、円滑かつ継続的に運用されるように努めます。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、加盟店が本サービスの利用に関して被った損害に対して、債務不履行責任及び不法行為責任その他の法律上の賠償責任を負わないものとします。ただし加盟店がサービスの利用に関して、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の重大な過失により損害を被った場合においてはこの限りではありません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前項の規定による</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の責任は、第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >16</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">条</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >項の規定に基づいて</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が収受する本サービスの利用料金の</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >ヶ月分相当額を上限とし、それを超える額については、すべて免責されるものとします。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、加盟店に直接かつ現実に発生した損害以外の損害（使用機会の逸失、その他の一切の間接損害、特別損害、付随損害、派生損害、逸失利益を含みますが、これらには限定されません）については、いかなる責任も負いません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本条に基づく</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の責任は、下記の各号の要件が全て満たされている事を条件とします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">1</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >より要請を受けた場合に、本サービスの不具合の詳細を速やかに</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に通知する事。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">2</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >より要請を受けた場合に、不具合に関する追加の情報を提供し、その他合理的な協力を行う事。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">(</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt">3</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">)</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >加盟店が、第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >26</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の各号の禁止行為を行っていない事。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >28</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（制限事項、無保証及び免責）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt">第</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >27</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >条の定めにかかわらず、以下の制限事項、無保証事項により加盟店において発生した損害、対応費用等について、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、過失の有無を問わず、一切これを補償する義務を負わないものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >1. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは本サービスの開発時点におけるインターネット、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >OS</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >、ウェブブラウザ（以下、「利用環境」といいます。）の技術仕様に準拠して開発されています。そのため利用環境の技術仕様が変更になった場合には、情報の送信、掲載、変更、更新、削除が行なえず、又は不具合が発生する場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >2. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは、サイト間連携、メールの送信や</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >SNS</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >サイトへの情報送信等、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の管理範囲外であるサービスに接続して処理を行う機能を含んでいる場合がありますが、当該接続先が正常に稼働していない場合や仕様変更があった場合には、当該接続が正常に行えない場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >3. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >利用環境の技術仕様に追加、変更等が発生した場合には、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >はこれに</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >対応すべく本サービスの修正を無償で行いますが、その完了時期は保証されません。その間、本サービスが正常に利用できない場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >4. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前号の定めにかかわらず、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >が加盟店のために行った本サービスの追加·修正部分（以下、「カスタマイズ部分」といいます。）に関して、利用環境の技術仕様の変更により、カスタマイズ部分の修正を行わなければ同加盟店が本サービス又はカスタマイズ部分を正常に利用し得ない場合には、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >と加盟店で協議して同修正の委託条件を定めるものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >5. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは一般的な加盟店が通常想定する方法にて利用する場合に不具合が発生しないことを基準として開発されています。加盟店が仕様外の情報等を送信した場合には、情報の送信、掲載、変更、更新、削除が行なえず、又は不具合が発生する場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >6. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスにおけるデータの維持について、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は細心の注意を払い各種対策を行いますが、停電·落雷·地震·火災·風水害、データ保存機器の故障、プログラムの不具合·誤作動、開発·運用時の設定ミス、悪意ある第三者による攻撃等により、本サービス内のデータが失われる恐れがあります。本サービスに保存中のデータについては、加盟店が自らバックアップを行っておく必要があります。バックアップを行わない場合には、不意にデータが失われ、その復旧が不可能となる場合があります。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >7. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは、本サービスを利用して加盟店と取引関係に入る個人や企業（以下、「加盟店顧客」といいます。）の信用力を保証するものでは無く、代金の回収やその他の対応を代行するものではありません。加盟店と加盟店顧客との取引関係について、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は法的にも事実上も一切介在しません。加盟店顧客に対する債権の回収の責任は加盟店のみが負い、加盟店顧客に対する商品提供や役務の提供の責任は加盟店のみが負います。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >8. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスは情報システムとしての性質上常時継続稼働が保証されるものではありません。</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >の過失の有無を問わず、本サービスが稼働していない事によって発生した加盟店の機会損失ついては、補償の対象となりません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >9. </span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >前各号に定めるものの他、</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、本サービスの仕様、品質、性能等について、その完全性、正確性、確実性、有用性、適法性、加盟店への特定の目的への適合性、商業性、市場性等を保証するものではありません。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; text-indent:0pt; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt">•</span
            ><span style="font:7.0pt 'Times New Roman'"
              >&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0;&#xa0; </span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >10.QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >は、加盟店の情報が外部に漏洩することの無いよう合理的な安全対</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >策を行いますが、加盟店が</span
            ><span style="font-family:AppleSystemUIFont; font-size:12pt"
              >QBAY</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >に提供した情報が漏洩しないことを保証するものではありません。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >29</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（準拠法）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約に基づく契約の成立、効力、解釈及び履行については、日本国法に準拠するものとします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >30</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（管轄裁判所）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >本サービスに関する一切の紛争については、</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt">埼玉</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >地方裁判所</span
            ><span style="font-family:'.PingFang SC'; font-size:12pt"
              >を第一審の専属管轄裁判所とします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >第</span
            ><span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >31</span
            ><span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >条（協議事項）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >本規約に定めのない事項又は疑義が生じた場合は、当事者が協議のうえ解決を図るものとします。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >&#xa0;</span
            >
          </p>
        </div>
      </div>
      <div v-if="index == 2">
        <div>
          <p style="margin:0pt; orphans:0; widows:0">
            <span class="title">個人情報保護方針</span>
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >当社は、</span
            >
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >WEB</span
            >
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >サービス事業、</span
            >
            <span style="font-family:AppleSystemUIFont; font-size:12pt"
              >WEB</span
            >
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >制作事業、人材紹介派遣事業を通じて社会に貢献することを使命とし、サービス品質の向上、お客様満足度の向上に向け努力しております。また、コンプライアンス重視、法令遵守は当然のこと、当社が個人情報を保護する事は、欠かすことの出来ない社会的責務であると考えてお</span
            >
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >ります。そのため、全従業者に個人情報保護の重要性を認識させ、個人情報が常に適切に取扱われるよう周知徹底しております。当社の個人情報保護に対する取り組みの基本姿勢を、以下の通り宣言いたします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >1</span
            >
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >．個人情報の取得·利用·提供</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >当社は取得する個人情報の利用目的を特定し、その利用目的を達成するために必要な範囲でのみ個人情報を取得します。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >当社は、個人情報を直接ご本人様から取得する場合、その利用目的や取扱いについて、文書あるいはそれに代わる方法でご同意いただいております。（お客様のみならず、従業員、取引先様も含めて、当社が事業の用に供する全ての個人情報を対象とします。）</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >また、次の場合をのぞき、利用目的の達成に必要な範囲を超えた取扱い（以下「目的外利用」）及び、本人の承諾を得ずに第三者に開示·提供することはいたしません。これらのことを防止する措置を、当社の個人情報保護マネジメントシステムによって確実に実施します。</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >（ａ）法令に基づく場合</span
            >
            <span style="font-family:'MS Gothic'; font-size:12pt"></span>
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >（ｂ）人の生命、身体又は財産の保護のために必要がある場合であって、本人の同意を得ることが困難であるとき</span
            >
            <span style="font-family:'MS Gothic'; font-size:12pt"></span>
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >（ｃ）公衆衛生の向上又は児童の健全な育成の推進のために特に必要がある場合であって、本人の同意を得ることが困難であるとき</span
            >
            <span style="font-family:'MS Gothic'; font-size:12pt"></span>
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >（ｄ）国の機関もしくは地方公共団体又はその委託を受けた者が法令の定める事務を遂行することに対して協力する必要がある場合であって、本人の同意を得ることによって当該事務の遂行に支障を及ぼすおそれがあるとき</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >2.</span
            >
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >法令、国が定める指針その他規範の遵守</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >当社は、個人情報に関する法令、国が定める指針その他規範を遵守いたします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >3.</span
            >
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >個人情報の漏えい、滅失又はき損の防止及び是正について</span
            >
          </p>
          <p style="margin:0pt; orphans:0; widows:0">
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >当社は、個人情報の漏えい、滅失又はき損の防止及び是正に関して、必要かつ適</span
            >
            <span style="font-family:'.PingFang SC'; font-size:12pt"
              >切な安全対策を実施いたします。</span
            >
          </p>
          <p style="margin:0pt 0pt 2pt; orphans:0; widows:0">
            <span
              style="font-family:AppleSystemUIFontBold; font-size:14pt; font-weight:bold"
              >4.</span
            >
            <span
              style="font-family:'.PingFang SC'; font-size:14pt; font-weight:normal"
              >個人情報に関する苦情および相談について</span
            >
          </p>
